import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/propiedades",
    name: "Propiedades",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Propiedades.vue");
    }
  },
  {
    path: "/agentes",
    name: "Agentes",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Agentes.vue");
    }
  },
  {
    path: "/donaciones",
    name: "Donaciones",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Donaciones.vue");
    }
  },
  {
    path: "/login",
    name: "Login",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Login.vue");
    }
  },
  {
    path: "/registro",
    name: "Registro",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Registro.vue");
    }
  },
  {
    path: "/contacto",
    name: "Contacto",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Contacto.vue");
    }
  },  
  {
    path: "/listapropiedades",
    name: "Listapropiedades",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Listapropiedades.vue");
    },
  },
  {
    path: "/detalles",
    name: "Detalles",
    component: function () {
      return import(/* webpackChunkName: "about" */ "../views/Detalles.vue");
    },
  }

];

const router = new VueRouter({
  routes,
});

export default router;
